.App {
    text-align: center;
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Consolas';
    font-size: calc(72px + 2vmin);
    color: white
}

button {
    width: 60%;
    font-size: calc(16px + 2vmin)
}

.history-column {
    font-size: calc(16px + 2vmin)
}

li {
    list-style-type: none;
    text-align: center
}

ul {
    padding-inline-start: 0;
}